import React, { useEffect, useState } from 'react';
import ContactForm from './ContactForm';
import Contact from './Contact';
import ContactFilter from './ContactFilter';
import Notification from './Notification';
import contactService from './Services';

function App() {
  const defaultName = "enter a name";
  const defaultNumber = "enter a number";

  const [contacts, setContacts] = useState([]);
  const [filterBy, setFilterBy] = useState("");
  const [newName, setNewName] = useState(defaultName);
  const [newNumber, setNewNumber] = useState(defaultNumber);
  const [errorMessage, setErrorMessage] = useState("");

  const handleInputChange = (setter) => (e) => setter(e.target.value);

  const resetInputs = () => {
    setNewName(defaultName);
    setNewNumber(defaultNumber);
  }

  const addContact = (e) => {
    e.preventDefault();
    
    if (newName === defaultName && newNumber === defaultNumber) {
      setErrorTimeout('Please enter non-default values for contact');
      return;
    }

    let existingContact = contacts.find(c => c.name === newName || c.number === newNumber);

    if (existingContact) {
      let confirm = window.confirm(`This name already exists in your contacts. Do you want to replace the number?`);
      if (confirm) {
        contactService.update(existingContact.id, { ...existingContact, number: newNumber })
                      .then(_ => resetInputs())
                      .catch(err => setErrorMessage(err));
      }
      return;
    }

    let newContact = {
      id: Math.round(Math.random() * 100000),
      name: newName,
      number: newNumber
    };

    contactService.create(newContact)
                  .then(returnedContact => {
                    setContacts(contacts.concat(returnedContact))
                    resetInputs();
                  })
                  .catch(err => setErrorTimeout("could not create new contact"));
  
  }

  const handleFilter = (e) => {
    let input = e.target.value;
    setFilterBy(input.trim().toLowerCase());
  }

  const handleDelete = (e) => {
    let id = e.target.closest('div').id;
    contactService.drop(id)
                  .catch(err => setErrorTimeout("contact was already deleted from server"));
  }

  const setErrorTimeout = (message) => {
    setErrorMessage(message);
    setTimeout(() => setErrorMessage(null), 3500);
  }

  useEffect(() => {
    contactService
      .getAll()
      .then(initalContacts => setContacts(initalContacts))
      .catch(err => setErrorTimeout('could not retrieve contacts from server'));
  }, []);

  const contactsToShow = contacts.filter(c => c.name.toLowerCase().includes(filterBy) || c.number.includes(filterBy));

  return (
    <div>
      <Notification message={errorMessage}/>
      <h1>Your contacts</h1>
      <ContactFilter handleFilter={handleFilter} />
      <h1>Add a New Contact:</h1>
      <ContactForm newName={newName}
        newNumber={newNumber}
        addContact={addContact}
        handleNameChange={handleInputChange(setNewName)}
        handleNumberChange={handleInputChange(setNewNumber)} />
      <h2>Contacts</h2>
      {contactsToShow.map(c => <Contact key={c.id} handleDelete={handleDelete} {...c} />)}
    </div>
  );
}

export default App;
